import Dexie from 'dexie';

export const dbFace = new Dexie('biometrex_face');
dbFace.version(1).stores({
    subjects: '++id, key',
});

export async function countFaceSubjects(){
    return  await dbFace.subjects
        .count();
}

export async function deleteFaceAllSubjects(){
    await dbFace.subjects
        .clear();

    console.log('delete existing')
}

export async function getFaceAllSubjects(){
    return dbFace.table('subjects').toArray();
}

export async function saveFaceSubjectDetection(key, faceDescription){
    const exist =  await dbFace.subjects
        .where("key")
        .equals(key)
        .first();

    if(!exist){
        console.warn('try to set face data to unexisted record')
        return;
    }

    console.log('save face for subject', faceDescription)
    await dbFace.subjects.update(exist.id, {
        faceDescription: exist.faceDescription.concat(faceDescription)
    });

}

export async function initFaceSubjectRow(key, personFiles, strategy ){
    console.log('init for ', key, strategy)
    const exist =  await dbFace.subjects
        .where("key")
        .equals(key)
        .first();

    const newFaceImage = personFiles.map( file => new Blob([file],{type: file.type}));

    if(exist && strategy !== 'override'){
        console.log('exist, strategy', strategy, exist);

        //todo unique same image
        await dbFace.subjects.update(exist.id, {
            personFiles: exist.personFiles.concat(newFaceImage)
        })
    }
    else{
        if(exist){
            console.log('exist, strategy', strategy, exist)
            await dbFace.subjects
                .where("key")
                .equals(key)
                .delete();

            console.log('delete existing')
        }

        console.log('no exist, strategy')
        await dbFace.subjects.add({
            key: key,
            personFiles: newFaceImage,
            faceDescription: []
        });

        console.log('added')
    }
}