<template>
  <div style="display: block; height: 45px" id="bio_selector">
    <va-select :change="bioChange()"  style="float: left;margin-left: 26px; width: 200px" v-model="bioSelectValue" :options="bioOptions"
               label="Biométrie"/>
  </div>
  <div id="nav" style="margin-top: 25px;margin-bottom: 40px;">
    <va-button style="float: left;margin-left: 26px;" outline class="mr-4 mb-2">{{ apiKey?.name || '---' }}</va-button>
    <span v-if="bioSelectValue.value === 'face' && currentRoute.includes('/')">
      <va-button :color="activeIf('/face-search')" @click="goto('/face-search')" style="width: 140px">
        <va-icon style="display: inline-block; margin-right: 5px" name="mood" color="white"
        />
        {{ 'Rechercher'.toUpperCase() }}
      </va-button>
      <span style="display: inline-block; width: 5px"> </span></span>

    <span v-if="bioSelectValue.value === 'face' && currentRoute.includes('/')">
      <va-button :color="activeIf('/face-train')" @click="goto('/face-train')" style="width: 140px">
        <va-icon style="display: inline-block; margin-right: 5px" name="mood" color="white"
        />
        {{ 'Entraîner'.toUpperCase() }}
      </va-button>
    </span>

    <span v-if="isWinForm && bioSelectValue.value === 'fingerprint' && currentRoute.includes('fingerprint')">
      <va-button :color="activeIf('/fingerprint-search')" @click="goto('/fingerprint-search')" style="width: 140px">
        <va-icon style="display: inline-block; margin-right: 5px" name="front_hand" color="white"
        />
        {{ 'Rechercher'.toUpperCase() }}
      </va-button>
      <span style="display: inline-block; width: 5px"> </span></span>

    <span v-if="isWinForm && bioSelectValue.value === 'fingerprint' && currentRoute.includes('fingerprint')">
      <va-button :color="activeIf('/fingerprint-train')" @click="goto('/fingerprint-train')" style="width: 140px">
        <va-icon style="display: inline-block; margin-right: 5px" name="front_hand" color="white"
        />
        {{ 'Entraîner'.toUpperCase() }}
      </va-button>
    </span>
  </div>
  <div id="app_view">
    <router-view/>
  </div>
</template>

<script>

import {getAPiKey} from "@/services/const";
import $ from "jquery";
export default {
  name: "App",
  async mounted() {
   this.checkSignIn()
  },

  watch: {
    '$route' (to, from) {
      console.log(to, from)
      this.currentRoute = to.fullPath;
    }
  },
  data(){
    const isWinForm = !!(window?.chrome?.webview?.hostObjects);

    const bioOptions = [
      {
        text: 'Visage',
        value: 'face',
      },
    ];

    if(isWinForm){
      bioOptions.push({
        text: 'Emprunt',
        value: 'fingerprint',
      })
    }

    return {
      apiKey: getAPiKey(),
      currentRoute: '',
      isWinForm: isWinForm,
      bioSelectValue: {
        text: 'Visage',
        value: 'face',
      },
      bioOldValue: {
        text: 'Visage',
        value: 'face',
      },
      bioOptions: bioOptions
    }
  },
  methods: {
    activeIf(route){
      if(this.currentRoute === route){
        return 'danger'
      }
      else{
        return 'primary'
      }
    },
    bioChange(){
      if(this.bioSelectValue.value !== this.bioOldValue.value && this.bioSelectValue.value === 'face'){
        this.goto('/face-search');
        this.bioOldValue = this.bioSelectValue;
      }

      if(this.bioSelectValue.value !== this.bioOldValue.value && this.bioSelectValue.value === 'fingerprint'){
        this.goto('/fingerprint-search');
        this.bioOldValue = this.bioSelectValue;
      }
    },
    checkSignIn(){
      const apiKey = getAPiKey();

      if(!apiKey){
        this.$vaToast.init({ message: 'APiKey Incorrect ou manquant', color: 'danger', duration: 70000000 });
        $("#app").remove();

      }
      else{
        // sessionStorage.setItem('apiKey', apiKey.value)
      }
    },
    goto(path) {
      this.$router.push(path)
    },
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 20px;
}

#app_view{

}

#bio_selector .va-input-wrapper{
  cursor: default;
}
</style>
