import { createWebHistory, createRouter } from "vue-router";
import FaceSearchPage from "@/pages/FaceSearchPage";
import FaceTrainPage from "@/pages/FaceTrainPage";
import FingerPrintTrainPage from "@/pages/FingerPrintTrainPage";
import FingerPrintSearchPage from "@/pages/FingerPrintSearchPage";

const routes = [
    {
        path: '/',
        redirect: '/face-search'
    },
    {
        path: "/face-search",
        name: "FaceSearchPage",
        component: FaceSearchPage,
    },
    {
        path: "/face-train",
        name: "FaceTrainPage",
        component: FaceTrainPage,
    },
     {
        path: "/fingerprint-search",
        name: "FingerPrintSearchPage",
        component: FingerPrintSearchPage,
    },
    {
        path: "/fingerprint-train",
        name: "FingerPrintTrainPage",
        component: FingerPrintTrainPage,
    },

];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;