import Dexie from 'dexie';

export const dbFingerPrint = new Dexie('biometrex_fingerprint');

dbFingerPrint.version(1).stores({
    subjects: '++id, key',
});

export async function countFingerPrintSubjects(){
    return  await dbFingerPrint.subjects
        .count();
}

export async function deleteFingerPrintAllSubjects(){
    await dbFingerPrint.subjects
        .clear();

    console.log('delete existing')
}

export async function getFingerPrintAllSubjects(){
    return dbFingerPrint.table('subjects').toArray();
}

export async function saveFingerPrintSubjectDetection(key, fingerprintDescription){
    const exist =  await dbFingerPrint.subjects
        .where("key")
        .equals(key)
        .first();

    if(!exist){
        console.warn('try to set fingerprint data to unexisted record')
        return;
    }

    console.log('save fingerprint for subject', fingerprintDescription)
    await dbFingerPrint.subjects.update(exist.id, {
        fingerprintDescription: exist.fingerprintDescription.concat(fingerprintDescription)
    });

}

export async function initFingerPrintSubjectRow(key, personFiles, strategy ){
    console.log('init for ', key, strategy)
    const exist =  await dbFingerPrint.subjects
        .where("key")
        .equals(key)
        .first();

    const newFingerPrintImage = personFiles.map( file => new Blob([file],{type: file.type}));

    if(exist && strategy !== 'override'){
        console.log('exist, strategy', strategy, exist);

        //todo unique same image
        await dbFingerPrint.subjects.update(exist.id, {
            personFiles: exist.personFiles.concat(newFingerPrintImage)
        })
    }
    else{
        if(exist){
            console.log('exist, strategy', strategy, exist)
            await dbFingerPrint.subjects
                .where("key")
                .equals(key)
                .delete();

            console.log('delete existing')
        }

        console.log('no exist, strategy')
        await dbFingerPrint.subjects.add({
            key: key,
            personFiles: newFingerPrintImage,
            fingerprintDescription: []
        });

        console.log('added')
    }
}